.root {
    background-color: #fff;
    border: 1px solid #dedede;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 15px;
    position: relative;
}

.root:hover {
    border: 1px solid #4361ee;
    box-shadow: 0 0 0 1px #4361ee;
}

.root:hover .editButtons {
    opacity: 1;
}

.rootFull:hover {
    background-color: #fff;
    border: 1px solid #dedede;
    box-shadow: none;
}

.image {
    width: 100%;
    height: 300px;
    object-fit: cover;
}

.imageFull {
    min-height: 300px;
    height: 100%;
}

.wrapper {
    padding: 10px 20px 20px;
}

.content {
    margin: 30px 0 50px;
}

.content p {
    font-size: 22px;
    line-height: 36px;
}

.indention {
    padding-left: 40px;
}

.title {
    font-size: 28px;
    margin: 0;
}

.title a {
    text-decoration: none;
    color: #000;
}

.title a:hover {
    color: #4361ee;
}

.titleFull {
    font-size: 42px;
    font-weight: 900;
}

.tags {
    list-style: none;
    padding: 0;
    margin: 5px 0 0 0;
}

.tags li {
    display: inline-block;
    font-size: 14px;
    margin-right: 15px;
    opacity: 0.5;
}

.tags li:hover {
    opacity: 1;
}

.tags li a {
    text-decoration: none;
    color: #000;
}

.postDetails {
    list-style: none;
    padding: 0;
    margin: 20px 0 0 0;
}

.postDetails li {
    display: inline-flex;
    align-items: center;
    font-size: 14px;
    margin-right: 20px;
    opacity: 0.5;
}

.postDetails li svg {
    font-size: 18px;
    margin-right: 5px;
}

.skeleton {
    background-color: #fff;
    border: 1px solid #dedede;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 15px;
}

.skeletonContent {
    padding: 20px;
}

.skeletonInfo {
    margin-left: 50px;
}

.skeletonUser {
    display: flex;
}

.skeletonDetails {
    display: flex;
    flex-direction: column;
}

.skeletonTags {
    display: flex;
}

.skeletonTags span {
    margin-right: 15px;
}

.editButtons {
    position: absolute;
    right: 15px;
    top: 15px;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    opacity: 0;
    transition: all 0.15s ease-in-out;
}

/* SKELETON CSS */
  
.stack {
    margin-bottom: 1rem;
}

.skeletonRectangular {
    width: 100%;
    height: 300px;
    background-color: #f0f0f0; 
}

.skeletonContent {
    display: flex;
    flex-direction: column;
}

.skeletonUser {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
}

.skeletonCircular {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #f0f0f0; 
    margin-right: 10px;
}

.skeletonUserDetails {
    display: flex;
    flex-direction: column;
}

.skeletonText {
    width: 100%;
    height: 20px;
    background-color: #f0f0f0; 
    margin-bottom: 0.25rem;
}

.skeletonInfo {
    margin-top: 0.5rem;
}

.skeletonTags {
    display: flex;
}
  
  